
  
.bookingtable{
    text-align: center;
  }
    
  .title{
      text-align: center;
      margin-top: 15px;
      font-family: cursive;
      font-style: italic;
  }
  
  
  
  
  .actions {
    display: flex;
    justify-content: flex-end;
    gap: 1rem;
    margin-top: 10px;
  }
  
  .actions button {
    font: inherit;
    color: #5a1a01;
    cursor: pointer;
    background-color: transparent;
    border: none;
    border-radius: 25px;
    padding: 0.5rem 2rem; 
  }
  
  .actions button:hover,
  .actions button:active {
    background-color: #ffe6dc;
  }
  
  .actions .submit {
    border: 1px solid #5a1a01;
    background-color: #5a1a01;
    color: white;
  }
  
  .actions .submit:hover,
  .actions .submit:active {
    background-color: #7a2706;
  }
  
  
  .admin-icon {
    color: green; /* Green color for admin icon */
  }
  
  .non-admin-icon {
    color: red; /* Red color for non-admin icon */
  }
  
  
  .blow-up-button{
    background-color: red;
  }