/* Hide the scrollbar in webkit-based browsers for a specific component */
.yourComponentClass::-webkit-scrollbar {
    width: 0;
  }
  
  /* Optional: Style the scrollbar track and thumb if needed */
  .yourComponentClass::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  .yourComponentClass::-webkit-scrollbar-thumb {
    background: #888;
  }
  
  /* Ensure content still scrolls for the specific component */
  .yourComponentClass {
    overflow-y: scroll;
  }
  


.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 20;
    background-color: rgba(0, 0, 0, 0.75);
  }
  
  .modal {
    position: fixed;
    top: 15vh;
    left: 5%;
    width: 90%;
    max-height: 60vh;
    background-color: white;
    padding: 1rem;
    border-radius: 14px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 30;
    animation: slide-down 300ms ease-out forwards;
    overflow: auto;
  }
  
  @media (min-width: 768px) {
    .modal {
      width: 30rem;
      left: calc(50% - 20rem);
      max-height: 75vh;
    }
  }
  
  @keyframes slide-down {
    from {
      opacity: 0;
      transform: translateY(-3rem);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }